import * as yup from "yup";

yup.addMethod( yup.mixed, 'optional', function optional() {
    return this.transform( ( value ) => {
        return !value ? undefined : value;
    } );
} );

export const OptionSchema = yup.object().shape( {
    enable     : yup.boolean().required(),
    breakpoints: yup.boolean().required(),
    prefix     : yup.string().optional().matches(
        /^[A-Za-z][A-Za-z0-9]*(?:[A-Za-z0-9-_]+)*$/,
        "Must start with a character and contain characters, numbers, underscores and dashes" )
} )
