import * as React from "react"
import { FunctionComponent, MouseEventHandler, useState } from "react";
import Icon from "../Icon";
import { evaIconsFill } from "../IconsLibs/iconsEvaFill";
import { createLabel } from "../../utilities";
import { motion } from "framer-motion";

interface OptionProps {
    defaultOptions: {}
    optionName: string
    innerRef: any
    errors?: any
    withPrefix?: boolean
    isOpen?: boolean
    onClick?: MouseEventHandler
}

const variants = {
    hidden : {
        height: 0,
    },
    visible: {
        height    : "auto",
        transition: {
            type           : "spring",
            damping        : 12,
            mass           : 0.5,
            staggerChildren: 0.1,
        },
    }
}
const variantsChild = {
    hidden : {
        opacity   : 0,
        transition: {
            type   : "spring",
            damping: 12,
            mass   : 0.5
        }
    },
    visible: {
        opacity   : 1,
        transition: {
            type   : "spring",
            damping: 12,
            mass   : 0.5
        }
    }
}

const Option: FunctionComponent<OptionProps> = ( { optionName, innerRef, errors, withPrefix = true, defaultOptions, isOpen, onClick } ) => {
    return (
        <div id={ optionName } className={ "pt--xs" } style={ { scrollMarginTop: 60 } }>
            <div className={ "flex bg--bg p--md cursor--pointer" + (isOpen ? " round--top--sm" : " round--sm") } onClick={ onClick }>
                <h3 className={ `m--0 ${ isOpen ? "text--primary" : "text--textDimmed" }` }>{ createLabel( optionName ) }</h3>
                <Icon className={ `ml--auto ${ isOpen ? "fill--primary " : "fill--text " }` } icon={ isOpen ? evaIconsFill.chevron_down : evaIconsFill.chevron_right }/>
            </div>
            <motion.div className={ `accordion__content round--bottom--sm relative` }
                        variants={ variants }
                        initial={ "hidden" }
                        animate={ isOpen ? "visible" : "hidden" }
            >
                <Switch optionName={ optionName } name={ `enable` } innerRef={ innerRef }/>
                <Switch className={ "brd-top brd--border" } optionName={ optionName } name={ `breakpoints` } innerRef={ innerRef }/>
                { optionName === "color-styles" && <Switch className={ "brd-top brd--border" } optionName={ optionName } name={ `useBgText` } innerRef={ innerRef }/> }
                { withPrefix && <PrefixInput optionName={ optionName } name={ `prefix` } innerRef={ innerRef } errors={ errors } defaultOptions={ defaultOptions }/> }
            </motion.div>
        </div>
    )
}

export default Option;

interface SwitchProps {
    name: string
    className?: string
    optionName: string
    innerRef: any
}

const Switch: FunctionComponent<SwitchProps> = ( { name, innerRef, optionName, className } ) => {
    const finalName = `${ optionName }.${ name }`;
    return (
        <motion.div variants={ variantsChild } className={ `switch flex--center jf__cnt--between bg--bg p--md ${ className ? className : "" }` }>
            <span className={ "text--textDimmed" }>{ createLabel( name ) }</span>
            <input className="switch__checkbox" id={ finalName } type={ "checkbox" } ref={ innerRef } name={ finalName }/>
            <label className={ "switch__label sm ml--auto" } htmlFor={ finalName }>
                <span className="sr-only">{ optionName + " " + createLabel( name ) }</span>
                <span className={ `switch__button flex--center jf__cnt--center` }/>
            </label>
        </motion.div>
    )
}

interface PrefixProps {
    name: string
    optionName: string
    innerRef: any
    defaultOptions: {}
    errors?: {
        [key: string]: {
            message: string
        }
    },

}

const PrefixInput: FunctionComponent<PrefixProps> = ( { name, innerRef, errors, optionName, defaultOptions } ) => {
    const finalName = `${ optionName }.${ name }`;
    return (
        <>
            <motion.div variants={ variantsChild } className={ "bg--bg px--md py--sm brd-top brd--border" }>
                <div className={ "flex--center jf__cnt--between " }>
                    <label className={ "text--textDimmed" } htmlFor={ finalName }>{ createLabel( name ) }</label>
                    <input aria-autocomplete={ "none" } id={ finalName } className={ "bg--input ml--auto w--25 round--sm fira py--xs" } type="text" ref={ innerRef } name={ finalName } placeholder={ defaultOptions[optionName][name] ? defaultOptions[optionName][name] : "null" }/>
                </div>
                { errors && errors[name] && <span className={ "block text--center mt--sm text--danger" }>{ errors[name].message }</span> }
            </motion.div>

        </>

    )
}

