import * as React from "react"
import SEO from "../../components/seo";
import * as yup from 'yup';
import SyntaxHighlight from "../../components/SynaxHighlight";
import { useState } from "react";
import { useForm } from "react-hook-form";
import Option from "../../components/Options/CustomizeOptions";
import { defaultOptions } from "../../components/Options/DefaultOptions";
import { OptionSchema } from "../../components/Options/OptionsValidation";
import scrollTo from 'gatsby-plugin-smoothscroll';

const Options = () => {
    const [ open, setOpen ] = useState( null )
    const [ optionsString, setOptionsString ] = useState( "" );
    const { register, handleSubmit, errors, setError, formState, getValues } = useForm( {
        validationSchema: validationSchema,
        defaultValues   : defaultOptions,
        mode            : "onChange"
    } );

    // const prepareSchema = Object.keys( defaultOptions ).map( ( key ) => {
    //     return `"${ key }": OptionSchema,\n`
    // } )
    // console.log( prepareSchema.join( '\n' ) );

    const onSubmit = ( values ) => {
        const prepare = Object.keys( values ).sort().map( ( key ) => {
            let str = `    $enable__${ key }: ${ values[key].enable },\n`
            str += `    $${ key }__use-breakpoints: ${ values[key].breakpoints },\n`
            str += values[key].prefix ? `    $${ key }__prefix : "${ values[key].prefix }", \n` : ""
            str += values[key].useBgText ? `    $${ key }__use-bgText : ${ values[key].useBgText }, \n` : ""
            return str
        } );
        setOptionsString( prepare.join( '\n' ) )
    }

    return (
        <>
            <SEO title={ "Utilities" }/>
            <h1>Options</h1>
            <p>You can enable / disable features and customize the class prefixes of the framework here. <br/>
                Copy and paste the generated options and add the @use before your initial load.</p>
            <form onSubmit={ handleSubmit( onSubmit ) }>
                {/*<div className={ 'grid' } style={ { gridTemplateColumns: "repeat(auto-fit, minmax(380px, 1fr))", gap: 20 } }>*/ }
                { Object.keys( defaultOptions ).sort().map( ( option, index ) => (
                    <Option
                        onClick={ () => setOpen( index === open ? null : index ) }
                        isOpen={ open === index }
                        key={ option }
                        optionName={ option }
                        innerRef={ register }
                        errors={ errors[option] }
                        defaultOptions={ defaultOptions }
                        withPrefix={ defaultOptions[option].prefix !== undefined }
                    />
                ) ) }
                {/*</div>*/ }
                <button className={ "button fill fullwidth primary my--md" } type={ "submit" }>Generate Options</button>
            </form>
            <SyntaxHighlight lang={ "scss" } codeString={ `@use "~skil3e-css/options.scss" with (
${ optionsString }
);` }/>

        </>
    )
}

export default Options


const validationSchema = yup.object().shape( {
    "align-content"     : OptionSchema,
    "align-items"       : OptionSchema,
    "align-self"        : OptionSchema,
    "justify-content"   : OptionSchema,
    "justify-items"     : OptionSchema,
    "justify-self"      : OptionSchema,
    "borders"           : OptionSchema,
    "border-radius"     : OptionSchema,
    "color-styles"      : OptionSchema,
    "display"           : OptionSchema,
    "flex-direction"    : OptionSchema,
    "flex-fill"         : OptionSchema,
    "flex-grow"         : OptionSchema,
    "flex-shrink"       : OptionSchema,
    "flex-wrap"         : OptionSchema,
    "flex-grid"         : OptionSchema,
    "flex-grid-offset"  : OptionSchema,
    "object-fit"        : OptionSchema,
    "object-position"   : OptionSchema,
    "position"          : OptionSchema,
    "position-placement": OptionSchema,
    "screen-readers"    : OptionSchema,
    "spacers"           : OptionSchema,
    "fontSizes"         : OptionSchema,
    "fontWeights"       : OptionSchema,
    "text-align"        : OptionSchema,
    "width"             : OptionSchema,
    "zIndex"            : OptionSchema,
} );


